import React, { useEffect, useState, useContext } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import { GlobalStyle } from './styles/globalStyles';
import ThemeToggle from './components/ThemeToggle';
import ThemeContext from './context/ThemeContext';
import HomeView from './views/HomeView';
import MouseFollowEffect from './components/MouseFollowEffect';
import smooth from './utils/smooth';
import link from './utils/link';
import Loader from './components/Loader';

const App = () => {
  const { isDarkMode } = useContext(ThemeContext);
  const [loading, setLoading] = useState(true);

  const handleLoaderComplete = () => {
    setLoading(false); //     
  };

  useEffect(() => {
    smooth();
    link();

    if (loading) {
      document.documentElement.classList.add('no-scroll'); //    
      document.body.style.overflow = 'hidden'; // body overflow: hidden 
    } else {
      document.documentElement.classList.remove('no-scroll'); //      
      document.body.style.overflow = ''; // body overflow  
    }

    //     
    return () => {
      document.documentElement.classList.remove('no-scroll');
      document.body.style.overflow = ''; //    
    };
  }, [loading]);

  return (
    <div className="App">
      <GlobalStyle /> {/* SVG   */}
      {/*       */}
      {loading && <Loader onComplete={handleLoaderComplete} />}
      <MouseFollowEffect isDarkMode={isDarkMode} />
      <BrowserRouter basename="/may-portfolio">
        <Routes>
          <Route path="/" element={<HomeView />} />
          {/*      */}
        </Routes>
      </BrowserRouter>
    </div>
  );
};

export default App;
