import React, { useEffect, useRef, useState } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { skillText } from '../constants';

// ScrollTrigger  
gsap.registerPlugin(ScrollTrigger);

const Skill = () => {
  const containerRef = useRef(null);
  const [visibleIndex, setVisibleIndex] = useState(0); //   title 
  const textRef = useRef([]);
  const descRef = useRef([]);

  useEffect(() => {
    const containerElement = containerRef.current;

    // ScrollTrigger 
    ScrollTrigger.create({
      trigger: containerElement,
      start: 'top top', //   
      end: '+=150%', //   
      scrub: true, //  
      pin: true, //  
      markers: false, //  
      onUpdate: self => {
        //      index 
        const newIndex = Math.floor(self.progress * skillText.length);
        setVisibleIndex(newIndex); // index 
      },
    });
  }, []);

  useEffect(() => {
    // GSAP  :    
    const currentTitle = textRef.current[visibleIndex];
    const currentDesc = descRef.current[visibleIndex];

    //   :   
    if (currentTitle) {
      gsap.fromTo(
        currentTitle,
        { opacity: 0.7, translateY: 10 }, //    
        { opacity: 1, translateY: 0, duration: 0.6, ease: 'circ.inOut', force3D: true }, //    
      );
    }

    if (currentDesc) {
      gsap.fromTo(
        currentDesc,
        { opacity: 0.5 }, //    
        { opacity: 1, duration: 0.6, ease: 'circ.inOut', force3D: true }, //    
      );
    }
  }, [visibleIndex]); // visibleIndex    

  return (
    <section id="skill" ref={containerRef}>
      <div className="skill__inner">
        <h2 className="skill__title">My skill</h2>
        <div className="skill__desc">
          <div>
            {skillText.map((skill, key) => (
              <div key={key} className="desc__wrap">
                <h3
                  ref={el => (textRef.current[key] = el)} // title  ref 
                  style={{
                    opacity: key === visibleIndex ? 1 : 0, //    
                    visibility: key === visibleIndex ? 'visible' : 'hidden', //    
                  }}
                >
                  <span>{key + 1}.</span> {skill.title}
                </h3>
                <p
                  ref={el => (descRef.current[key] = el)} // desc  ref 
                  style={{
                    opacity: key === visibleIndex ? 1 : 0, //    
                    visibility: key === visibleIndex ? 'visible' : 'hidden', //    
                  }}
                >
                  {skill.desc}
                </p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Skill;
