import React, { useEffect, useState } from 'react';
import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import { introText } from '../constants';

gsap.registerPlugin(ScrollTrigger);

const Intro = () => {
  const [splitText, setSplitText] = useState([]);

  useEffect(() => {
    //     JSX 
    const splitLetters = text => {
      return text.split('').map((letter, index) => {
        return letter === ' ' ? (
          <span key={index}> </span>
        ) : (
          <span key={index} className="letter">
            {letter}
          </span>
        );
      });
    };

    //       JSX 
    const splitLines = text => {
      const lines = text.split('\n'); //    
      return lines.map((line, index) => (
        <div key={index} className="text-line">
          {splitLetters(line)} {/*       */}
        </div>
      ));
    };

    //   
    const animateLetters = () => {
      const letterElements = document.querySelectorAll('.letter');
      const sectionTrigger = document.querySelector('.intro__inner');

      // GSAP  
      letterElements.forEach(letter => {
        gsap.fromTo(
          letter,
          {
            x: 0,
            y: 0,
            rotation: 0,
          },
          {
            x: gsap.utils.random(-500, 500), //   X
            y: gsap.utils.random(-1100, 500), //   Y
            rotation: gsap.utils.random(-138, 187), //  
            duration: 5.5,
            ease: 'circ.inOut', //  
            scrollTrigger: {
              trigger: sectionTrigger,
              start: 'top 0%', // intro  20%   
              end: 'bottom 80%', // intro  80%   
              markers: false, //   
              scrub: 1, //     
              toggleActions: 'restart none reverse none', //     
            },
          },
        );
      });
    };

    //     
    const text = introText.title;
    setSplitText(splitLines(text)); //   

    // `splitText`     
    const timer = setTimeout(() => {
      animateLetters(); //  
    }, 50); //      

    //      
    return () => clearTimeout(timer);
  }, []); //     

  return (
    <section id="intro">
      <div className="intro__inner">
        <h2 className="intro__title">
          {/* splitText      */}
          <div className="text">{splitText}</div>
        </h2>
        <div className="intro__lines bottom" aria-hidden="true">
          <span className="line"></span>
          <span className="line"></span>
          <span className="line"></span>
          <span className="line"></span>
          <span className="line"></span>
          <span className="line"></span>
          <span className="line"></span>
        </div>
      </div>
    </section>
  );
};

export default Intro;
